*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Open Sans', sans-serif;
}

a{
	text-decoration: none;
}

.navbar{
	width: 100%;
	height: 52px;
	background: #283e4a;
	padding: 0 30px;
}

.navbar__grid{
	display: grid;
	grid-template-columns: 2fr 3fr;
	max-width: 1100px;
	margin: auto;
	height: 100%;
	-webkit-align-items: center;
	        align-items: center;
}

path{
	fill: #dadada;
}

.fill-inside{
	fill:  #283e4a;
}

.navbar__search{
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}
.form{
	position: relative;
}
.search-icon{
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	left: 10px;
	display: -webkit-flex;
	display: flex;
}

.navbar__search input{
	width: 280px;
	height: 34px;
	border-radius: 4px;
	background: #e1e9ee;
	border: none;
	margin-left: 5px;
	padding: 0 8px 0 30px;
	font-size: 16px;
	color: #000;
}
.form{
	position: relative;
}
.show::-webkit-input-placeholder{
	display: block;
	opacity: 1;
}
.show::-ms-input-placeholder{
	display: block;
	opacity: 1;
}
.show::placeholder{
	display: block;
	opacity: 1;
}
.show{
	background: #e1e9ee !important;
}
.hide{
	background: #fff !important;
}

.hide::-webkit-input-placeholder{
	display: none;
	opacity: 0;
}

.hide::-ms-input-placeholder{
	display: none;
	opacity: 0;
}

.hide::placeholder{
	display: none;
	opacity: 0;
}

.showluv{
	display: -webkit-flex;
	display: flex;
}

.hideluv{
	display: none;
}

.after-search{
	position: absolute;
	top: 0;
	right: 0;
	height: 100%;
	width: 40px;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	background: #e1e9ee;
	border-top-right-radius: 3px;
	border-bottom-right-radius: 3px;
}

.showafter{
	display: -webkit-flex;
	display: flex;
}
.hideafter{
	display: none;
}
.after-search svg{
	margin: 0 auto;
}

.search__results{
	position: absolute;
	top: 115%;
	left: 0;
	width: 700px;
	background: #fff;
	padding: 15px;
	z-index: 9;
	box-shadow: 0px 0px 3px grey;
}

.search__results h4{
	color: #676666;
}

.search__category{
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	margin-top: 20px;
	color: grey;
}

.search__category svg path:first-child{
	fill: #fff;
}
.search__category svg path:nth-child(2){
	fill:#1ea9ca;
}
.search__category svg{
	margin-right: 20px;
}

.clear-title{
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	margin-top: 20px;
	border-top: 1px solid #dadada;
	padding-top: 20px;
	margin-bottom: 20px;
}

.search-people{
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	margin-bottom: 15px;
}

.showsearch{
	display: block;
}

.hidesearch{
	display: none;
}

.search-people img{
	width: 40px;
	height: 40px;
	border-radius: 50%;
	object-fit: cover;
	margin-right: 20px;
}

.navbar__search svg{
	width: 20px;
	height: 20px;
}

.navbar__search path{
	fill: #2f2f2f;
}

.navbar__search > img{
	width: 44px;
	height: 44px;
	margin: 0;
	padding: 0;
}

.navbar__right{
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	height: 100%;
	
}

.menu-icon{
	text-align: center;
	width: 80px;
	height: 100%;
	position: relative;
	padding-top: 5px;
}

.menu-icon > span{
	position: absolute;
	top: 5px;
	right: 20px;
	background: red;
	border-radius: 50%;
	font-size: 10px;
	font-weight: bold;
	color: #fff;
	width: 15px;
	height: 15px;
	text-align: center;
}

.menu-icon p{
	font-size: 12px;
	color: #dadada;
	line-height: 0.8;
}

.triangle{
	content: "";
	width: 10px;
	height: 7px;
	-webkit-clip-path: polygon(0 0, 50% 100%, 100% 0);
	        clip-path: polygon(0 0, 50% 100%, 100% 0);
	background: #dadada;
	position: absolute;
	top: 35px;
	right: 13px;
}

.me{
	width: 25px;
	height: 25px;
	background: black;
	border-radius: 50%;
	margin: 0 auto;
	object-fit: cover;
}
.height-normal{
	 line-height: 1;
    height: 100%;
    width: 103%;
    font-size: 13px;
    margin-top: 6px;
    color: #e8da8d;
}

.border-right{
	border-right: 1px solid grey;
}

.active{
	border-bottom: 3px solid #fff;
}

.active path{
	fill: #fff;
}

.active p{
	color: #fff;
}

/*_____________NAVBAR END_______________-*/

.home{
